@charset "UTF-8";
.fade {
  opacity: 0;
  transition: opacity 0.15s linear;
}
.fade.in {
  opacity: 1;
}

.collapse {
  display: none;
}
.collapse.in {
  display: block;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition-timing-function: ease;
  transition-duration: 0.35s;
  transition-property: height;
}

.nav-adventure {
  margin-top: -1px;
}
.nav-adventure .nav-item {
  display: block;
  margin-left: -18px;
  margin-right: -18px;
  border-bottom: 1px solid #e7e8e9;
}
.nav-adventure .nav-item:first-child {
  border-top: 1px solid #e7e8e9;
}
.nav-adventure .nav-item-title {
  line-height: 1;
  font-weight: 600;
  margin-bottom: 6px;
}
.nav-adventure .nav-item-description {
  margin-bottom: -5px;
}
.nav-adventure .nav-link {
  position: relative;
  display: flex;
  justify-items: flex-start;
  align-items: center;
  padding: 3px 18px;
  line-height: 20px;
  color: #555a62;
  transition: background-color 0.1s;
}
.nav-adventure .nav-link > * {
  z-index: 1;
}
.nav-adventure .nav-link svg {
  display: block;
  height: 24px;
}
.nav-adventure .nav-link:hover {
  background-color: #f9fdfe;
}
.nav-adventure .nav-link:hover::before {
  width: 100%;
}
.nav-adventure .nav-item-icon {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  border: 1px solid #e7e8e9;
  border-radius: 6px;
  margin-right: 18px;
}
.nav-adventure .nav-item-arrow {
  margin-left: auto;
}

.nav-stats {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.nav-stats-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 1;
  padding: 9px 18px 0;
  color: rgba(25, 25, 25, 0.2);
}

.nav-stats-link > * {
  cursor: pointer;
}

.nav-stats-title {
  font-weight: 400;
  padding: 9px 0 4.5px;
  text-transform: uppercase;
  font-size: 12px;
}

.nav-stats-value {
  font-weight: 300;
  font-size: 40px;
}

.nav-heading {
  margin-top: 9px;
  margin-bottom: 9px;
}
.nav-heading a {
  display: block;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 12px;
  border-bottom: 1px dotted rgba(0, 0, 0, 0.0875);
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
}
.nav-heading a::before {
  display: inline-block;
  width: 0;
  height: 0;
  margin-right: 4px;
  margin-left: 4px;
  vertical-align: middle;
  content: "";
  border-top: 0.375em solid;
  border-right: 0.375em solid transparent;
  border-left: 0.375em solid transparent;
  position: relative;
  top: -1px;
  -webkit-transform: rotate(0deg);
  tranform: rotate(0deg);
  margin-left: -16px;
  margin-right: 8px;
  transition: all 0.1s ease-out;
}
.nav-heading a.collapsed::before {
  -webkit-transform: rotate(-90deg);
  tranform: rotate(-90deg);
}

.nav-inline {
  font-size: 0;
}
.nav-inline .nav-item {
  display: inline-block;
}
.nav-inline .nav-item + .nav-item,
.nav-inline .nav-link + .nav-link {
  margin-left: 16px;
}

.panel-nav a {
  color: #949aa2;
}
.panel-nav .title {
  display: block;
  margin-bottom: 18px;
}
.panel-nav .badge {
  font-size: 11px;
}
.panel-nav .globals-sign-out {
  display: none;
}
@media (max-width: 991px) {
  .panel-nav .globals-sign-out {
    display: block;
    margin-top: 36px;
  }
}
.panel-nav .nav-pill {
  color: #f6f9fb;
  background: rgba(148, 154, 162, 0.6);
  display: inline-block;
  font-size: 8px;
  line-height: 1;
  padding: 2px 4px;
  border-radius: 3px;
  vertical-align: middle;
}
.panel-nav .nav-link.active {
  border-radius: 6px 0 0 6px;
}
.panel-nav .nav-link.active, .panel-nav .nav-link.active:hover {
  color: #555a62;
  background-color: #e8ecf2;
}
.panel-nav .nav-link.active .nav-pill, .panel-nav .nav-link.active:hover .nav-pill {
  color: #e8ecf2;
  background-color: rgba(255, 255, 255, 0.7);
}
.panel-nav .nav-link:hover, .panel-nav .nav-link.hover {
  color: #025c8a;
}
.panel-nav .nav-stacked .nav-item + .nav-item {
  margin-top: 0;
}

.nav-link, .nav-link.active, .nav-link:active, .nav-link.hover, .nav-link:hover {
  cursor: pointer;
}

.navbar .vanilla-logo {
  width: 65px;
}
.navbar .title {
  color: #fff;
}
.navbar .logo + .title {
  margin-left: 18px;
}
.navbar img {
  height: 36px;
}
.navbar .nav-item {
  text-align: center;
  border-right: 1px solid rgba(0, 0, 0, 0.125);
}
.navbar .nav-item + .nav-item {
  margin: 0;
}
.navbar .nav-item:first-child {
  border-left: 1px solid rgba(0, 0, 0, 0.125);
}
.navbar .nav {
  height: 100%;
}
.navbar .nav-link {
  position: relative;
  min-width: 180px;
  color: #fff;
  border-radius: 0;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar .nav-link.active, .navbar .nav-link.active:hover {
  background-color: #04a8fd;
}
.navbar .nav-link.active > *, .navbar .nav-link.active:hover > * {
  color: #fff;
}
.navbar .nav-link:hover {
  background-color: #04a8fd;
}
@media (max-width: 1199px) {
  .navbar .navbar-brand,
.navbar .navbar-memenu {
    min-width: inherit;
  }
}
@media (max-width: 991px) {
  .navbar {
    justify-content: flex-start;
  }
  .navbar .nav {
    display: flex;
    flex-grow: 1;
    justify-content: space-between;
  }
  .navbar .nav-item {
    width: 100%;
  }
  .navbar .nav-link {
    min-width: inherit;
    width: 100%;
    font-size: 12px;
  }
  .navbar .nav-link-description {
    display: none;
  }
  .navbar .navbar-memenu,
.navbar .navbar-brand {
    display: none;
  }
}

.drawer-toggle {
  color: #fff;
  font-size: 18px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.drawer-toggle:hover, .drawer-toggle:active, .drawer-toggle:focus {
  color: #fff;
  text-decoration: none;
  outline: 0;
}
.drawer-toggle:hover, .drawer-toggle:active {
  background-color: #04a8fd;
}

.navbar-memenu,
.navbar-brand {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 240px;
}

.navbar-profile {
  cursor: pointer;
}
.navbar-profile .icon-caret-down {
  border-top-color: #ceeeff;
  display: inline-block;
  width: 0;
  height: 0;
  margin-right: 4px;
  margin-left: 4px;
  vertical-align: middle;
  content: "";
  border-top: 0.375em solid;
  border-right: 0.375em solid transparent;
  border-left: 0.375em solid transparent;
}

.nav-icons {
  margin-right: 18px;
  color: #fff;
}
.nav-icons svg {
  display: block;
  height: 31.3333333333px;
}
.nav-icons .nav-link {
  min-width: inherit;
}

