  position: relative;
  overflow: hidden;
  text-align: left;
  pointer-events: initial !important;
}

.embedText-main {
  padding: 14px;
  box-sizing: border-box;
}

.embedText-header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  font-size: 14px;
  line-height: 1.25;
}

.embedText-titleLink {
  margin: 0;
  color: inherit;
}

.embedText .embedText-titleLink,
.embedText .embedText-title {
  margin-top: 0;
  margin-bottom: 4px;
  display: block;
  width: 100%;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.25;
  color: #555a62;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.richEditor .embedText-titleLink {
  pointer-events: none;
}

