.embedLink-image {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
  width: 225px;
}
@supports (object-fit: cover) {
  .embedLink-image {
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}
.embedLink-image + .embedText-main {
  margin-left: auto;
  width: calc(100% - 225px);
  border-left-style: solid;
  border-left-width: 1px;
  border-left-color: rgba(85, 90, 98, 0.1);
}
@media (max-width: 550px) {
  .embedLink-image {
    position: relative;
    width: 100%;
    padding-top: 0;
  }
  .embedLink-image + .embedText-main {
    width: 100%;
    border-right: none;
  }
}

.embedLink-source {
  font-size: 12px;
  color: #6f737a;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
}

.embedLink-excerpt {
  color: #555a62;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
  max-width: 100%;
  font-size: 14px;
  margin-top: 4px;
  line-height: 1.4;
  max-height: 58.8px;
  white-space: normal;
}

