.embedLoader-icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  flex-basis: 24px;
}

.embedLoader {
  display: block;
  position: relative;
}

.embedLoader-box {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.3);
  width: 135px;
  height: 135px;
  margin: auto;
  overflow: hidden;
}

.embedLoader-loader {
  transition-property: opacity;
  transition-duration: 0.15s;
  transition-timing-function: ease-out;
  display: block;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  border-top: 3px solid #0291db;
  border-right: 3px solid rgba(2, 145, 219, 0.3);
  border-bottom: 3px solid rgba(2, 145, 219, 0.3);
  border-left: 3px solid rgba(2, 145, 219, 0.3);
  transform: translateZ(0);
  animation: spinnerLoader 0.7s infinite ease-in-out;
}
.embedLoader-loader::after {
  border-radius: 50%;
  width: 42px;
  height: 42px;
}

.embedLoader-error {
  box-shadow: 0 1px 3px 0 rgba(85, 90, 98, 0.3);
  display: flex;
  overflow: hidden;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 15px;
  background: #ffebeb;
  transition: 0.2s ease box-shadow;
  cursor: pointer;
}
.embedLoader-error:hover {
  box-shadow: 0 0 0 2px rgba(2, 145, 219, 0.5) inset;
}
.embedLoader-error:focus {
  box-shadow: 0 0 0 2px #0291db inset;
}
.embedLoader-error + .embedLoader-error {
  margin-top: 12px;
}
.embedLoader-error .embedLoader-warningIcon {
  align-self: flex-start;
}
.embedLoader-error .closeButton {
  opacity: 0.7;
  width: 24px;
  height: 24px;
  flex-basis: 24px;
  align-self: flex-start;
  margin: 0 0 0 auto;
  cursor: pointer;
  position: relative;
}
.embedLoader-error .closeButton, .embedLoader-error .closeButton:hover, .embedLoader-error .closeButton:focus {
  background: none !important;
  border: 0 !important;
  color: inherit !important;
  line-height: normal;
  overflow: hidden;
  padding: 0 !important;
  -webkit-appearance: button !important;
  /* for input */
  -webkit-user-select: none !important;
  /* for button */
  -moz-user-select: none !important;
  -ms-user-select: none !important;
}
.embedLoader-error .closeButton:hover, .embedLoader-error .closeButton:focus {
  opacity: 1;
}
.embedLoader-error .closeButton .embedLoader-icon {
  position: absolute;
  top: 0;
  left: 0;
}

.userContent .embedLoader-errorMessage {
  max-width: calc(100% - 72px);
  width: 100%;
  padding-left: 24px;
}

.embedIFrame {
  position: relative;
  display: block;
  max-width: 100%;
  margin: auto;
}
.embedIFrame .embedIFrame-iframe {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

